//
// error-wrap
// --------------------------------------------------
// 模块：错误页
// 描述：错误页
// --------------------------------------------------

.error-wrap {
    padding: 50px 0;

    .error-message {
        padding: 20px 0;
    }
    .error-msg {
        font-size: @font-size-16;
        font-weight: 700;
        color: @brand-info;
    }
    .error-tips {
        font-size: @font-size-14;
        color: @gray;
    }
}
