//
// checkbox
// --------------------------------------------------
// 模块：checkbox插件
// 地址：https://github.com/flatlogic/awesome-bootstrap-checkbox
// --------------------------------------------------
@font-family-icon: 'FontAwesome';
@fa-var-check: "\f00c";
@check-icon: @fa-var-check;

.checkbox-variant(@parent, @color) {
    .@{parent} input[type="checkbox"]:checked + label,
    .@{parent} input[type="radio"]:checked + label {
        &::before {
            background-color: @color;
            border-color: @color;
        }
        &::after {
            color: #fff;
        }
    }
}

.checkbox-variant-indeterminate(@parent, @color) {
    .@{parent} input[type="checkbox"]:indeterminate + label,
    .@{parent} input[type="radio"]:indeterminate + label {
        &::before {
            background-color: @color;
            border-color: @color;
        }
        &::after {
            background-color: #fff;
        }
    }
}

.checkbox{
    padding-left: 20px;

    label{
        display: inline-block;
        vertical-align: middle;
        position: relative;
        padding-left: 5px;

        &::before{
            content: "";
            display: inline-block;
            position: absolute;
            width: 17px;
            height: 17px;
            left: 0;
            margin-left: -20px;
            border: 1px solid @input-border;
            border-radius: 3px;
            background-color: @input-bg;
            .transition(~"border 0.15s ease-in-out, color 0.15s ease-in-out");
        }

        &::after{
            display: inline-block;
            position: absolute;
            width: 16px;
            height: 16px;
            left: 0;
            top: 0;
            margin-left: -20px;
            padding-left: 3px;
            padding-top: 1px;
            font-size: 11px;
            color: @input-color;
            line-height: 1.4;
        }
    }

    input[type="checkbox"],
    input[type="radio"]{
        opacity: 0;
        z-index: 1;
        cursor: pointer;

        &:focus + label::before{
            .tab-focus();
        }

        &:checked + label::after{
            font-family: @font-family-icon;
            content: @check-icon;
        }

        &:indeterminate + label::after{
            display: block;
            content: " ";
            width: 10px;
            height: 3px;
            background-color: #555555;
            border-radius: 2px;
            margin-left: -16.5px;
            margin-top: 7px;
        }

        &:disabled {
            cursor: not-allowed;
        }

        &:disabled + label{
            opacity: 0.65;

            &::before{
                background-color: @input-bg-disabled;
                cursor: not-allowed;
            }
        }

    }

    &.checkbox-circle label::before{
        border-radius: 50%;
    }

    &.checkbox-inline{
        margin-top: 0;
    }
}

.checkbox-variant(checkbox-primary, @brand-primary);
.checkbox-variant(checkbox-danger, @brand-danger);
.checkbox-variant(checkbox-info, @btn-info-bg);
.checkbox-variant(checkbox-warning, @brand-warning);
.checkbox-variant(checkbox-success, @brand-success);

.checkbox-variant-indeterminate(checkbox-primary, @brand-primary);
.checkbox-variant-indeterminate(checkbox-danger, @brand-danger);
.checkbox-variant-indeterminate(checkbox-info, @btn-info-bg);
.checkbox-variant-indeterminate(checkbox-warning, @brand-warning);
.checkbox-variant-indeterminate(checkbox-success, @brand-success);

//
// Radios
// --------------------------------------------------

.radio-variant(@parent, @color) {
    .@{parent} input[type="radio"]{
        & + label{
            &::after{
                background-color: @color;
            }
        }
        &:checked + label{
            &::before {
                border-color: @color;
            }
            &::after{
                background-color: @color;
            }
        }
    }
}

.radio{
    padding-left: 20px;

    label{
        display: inline-block;
        vertical-align: middle;
        position: relative;
        padding-left: 5px;

        &::before{
            content: "";
            display: inline-block;
            position: absolute;
            width: 17px;
            height: 17px;
            left: 0;
            margin-left: -20px;
            border: 1px solid @input-border;
            border-radius: 50%;
            background-color: @input-bg;
            .transition(border 0.15s ease-in-out);
        }

        &::after{
            display: inline-block;
            position: absolute;
            content: " ";
            width: 11px;
            height: 11px;
            left: 3px;
            top: 3px;
            margin-left: -20px;
            border-radius: 50%;
            background-color: @input-color;
            .scale(0, 0);

            .transition-transform(.1s cubic-bezier(.8,-0.33,.2,1.33));
            //curve - http://cubic-bezier.com/#.8,-0.33,.2,1.33
        }
    }

    input[type="radio"]{
        opacity: 0;
        z-index: 1;
        cursor: pointer;

        &:focus + label::before{
            .tab-focus();
        }

        &:checked + label::after{
            .scale(1, 1);
        }

        &:disabled{
            cursor: not-allowed;
        }

        &:disabled + label{
            opacity: 0.65;

            &::before{
                cursor: not-allowed;
            }
        }

    }

    &.radio-inline{
        margin-top: 0;
    }
}

.radio-variant(radio-primary, @brand-primary);
.radio-variant(radio-danger, @brand-danger);
.radio-variant(radio-info, @btn-info-bg);
.radio-variant(radio-warning, @brand-warning);
.radio-variant(radio-success, @brand-success);

input[type="checkbox"],
input[type="radio"] {
    &.styled:checked + label:after {
        font-family: @font-family-icon;
        content: @check-icon;
    }
    &.styled:checked + label {
        &::before {
            color: #fff;
        }
        &::after {
            color: #fff;
        }
    }
}

.radio-single {
    border: 1px solid @sino-form-border;
    border-radius: 3px;
    .flexbox();
    span {
        width: 100%;
        padding: 2px 0;
        border-left: 1px solid @sino-form-border;
        text-align: center;
        cursor: pointer;
        &:first-child {
            border-left-width: 0;
        }
        &.active {
            background-color: @sino-form-border;
            color: @gray;
        }
    }
    &.radio-single-svg-2 {
        span {
            width: 50%;
        }
    }
    &.radio-single-svg-3 {
        span {
            width: 33.33333%;
        }
    }
    &.radio-single-svg-4 {
        span {
            width: 25%;
        }
    }
    &.radio-single-svg-5 {
        span {
            width: 20%;
        }
    }
}
.radio-single-variant(@parent, @color) {
    .@{parent} {
        border-color: @color;
        span {
            border-color: @color;
            &.active {
                background-color: @color;
                color: @white;
            }
        }
    }
}
.radio-single-variant(radio-single-primary, @brand-primary);
.radio-single-variant(radio-single-info, @btn-info-bg);
