//
// spinner
// --------------------------------------------------
// 模块：spinner插件
// 地址：https://github.com/vsn4ik/jquery.spinner
// 地址DEMO：https://vsn4ik.github.io/jquery.spinner/
// --------------------------------------------------
.spinner.input-group {
    .input-group-addon {
        padding: 2px 6px;
        &.input-group-addon-unit {
            border-left: 0;
        }
        &.input-group-addon-spin {
            padding: 0;
            background-color: transparent;
        }
        .spin-up,
        .spin-down {
            height: ((@input-height-base - 2) / 2);
            width: 22px;
            overflow: hidden;
            display: block;
            text-align: center;
            color: #999;
            &:hover {
                color: #555;
            }
            .fa {
                font-size: @font-size-16;
                margin-top: -6px;
                vertical-align: middle;
            }
        }

        a.spin-up,
        a.spin-down {
            text-decoration: none;
        }

        button.spin-up,
        button.spin-down {
            background: none;
            border: none;
            padding: 0;
        }
    }

    &.input-group-sm .input-group-addon {
        .spin-up,
        .spin-down {
            height: 8px;

            .fa {
                margin-top: -10px;
            }

            .glyphicon {
                font-size: 8px;
                top: -5px;
            }
        }
    }

    &.input-group-lg .input-group-addon {
        .spin-up,
        .spin-down {
            height: 12px;
            width: 12px;

            .fa {
                margin-top: -16px;
            }

            .glyphicon {
                font-size: 12px;
                top: -6px;
            }
        }
    }
}
