//
// upload
// --------------------------------------------------
// 模块：上传
// 类名：upload
// --------------------------------------------------

.upfile-group {
    position: relative;
    .form-control {
        padding-left: 90px;
    }
    .upfile-inner {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        .btn {
            position: relative;
            display: inline-block;
            padding-left: 15px;
            padding-right: 15px;
            overflow: hidden;
            &.btn-md {
                min-width: 106px;
            }
            &.btn-sm {
                padding-left: 5px;
                padding-right: 5px;
            }
        }
        .btn-upfile {
            width: @btn-upfile-width;
            position: relative;
            display: inline-block;
            background-color: #f5f5f5;
            padding: 4px 10px;
            margin-top: 1px;
            margin-left: 1px;
            overflow: hidden;
            color: @brand-primary;
            text-decoration: none;
            line-height: 20px;
            &:hover {
                background-color: @gray-lighter;
            }
        }
        input {
            position: absolute;
            font-size: 100px;
            right: 0;
            top: 0;
            .opacity(0);
        }
        img {
            width: 100%;
        }
    }
    &.upfile-group-md {
        .btn-upfile {
            width: 105px;
        }
    }
    &.upfile-group-plus {
        height: @btn-upfile-height;
        .btn-upfile {
            height: @btn-upfile-height;
            border: 1px solid @input-border;
            padding: 0;
            margin-top: 0;
            margin-left: 0;
            i.fa-plus {
                margin: @upfile-plus-margin;
                font-size: 50px;
                color: @input-border;
            }
        }
    }
    .upfile-files-text {
        margin-top: 36px;
        p {
            margin-bottom: 0;
        }
        .fa-close {
            cursor: pointer;
            width: 15px;
            height: 15px;
            background-color: @sino-form-border;
            border-radius: 100%;
            padding: 2px;
            font-size: @font-size-12;
            color: @white;
            text-align: center;
        }
    }
}
