//
// flow
// --------------------------------------------------
// 模块：公告
// 类名：notice
// --------------------------------------------------

.flow-dialog .modal-dialog {
    width: @flow-dialog-lg;
}
.flow-dialog .modal-dialog.modal-md {
    width: @flow-dialog-md;
}

.flow-box {
    padding: 50px 0;
    &:extend(.clearfix all);

    li {
        a {
            padding: 20px 5px;
            display: block;
            text-decoration: none;
            border: 1px solid @gray-lighter;
            color: @gray;
            i {
                color: @gray-light;
            }
            span {
                margin-top: 15px;
                min-height: 34px;
                display: block;
            }
            &:hover,
            &.active {
                color: @white;
                border-color: @flow-bg-color;
                background-color: @flow-bg-color;
                i {
                    color: @white;
                }
            }
        }
    }
}

.flow-title {
    font-weight: 700;
    p {
        margin-bottom: 5px;
    }
}

.flow-group {
    margin-left: 10px;
    margin-right: 10px;
    &:extend(.clearfix all);

    ul {
        margin-bottom: 0;
        &.flow-box-left {
            width: 11.1%;
        }
        &.flow-box-right {
            width: 88.9%;
        }
    }
    p {
        margin-bottom: 5px;
    }
    h3 {
        margin: 5px 0 10px 0;
        font-weight: 700;
    }
    .flow-group-inline {
        //margin-left: 20px;
        margin-bottom: 5px;
        &:extend(.clearfix all);
        .radio-inline, .checkbox-inline {
            padding-left: 0;
            margin-bottom: 5px;
            &.active{
                font-weight: 700;
                color: #f99912;
            }
        }
        .radio-inline + .radio-inline, .checkbox-inline + .checkbox-inline {
            margin-left: 0;
        }
    }
    textarea.form-control {
        width: 100%;
        border-radius: 0;
    }
    span.text-count{
        position:absolute;
        right: 6px;
        bottom: 6px;
        .text-count-word{
            color: @brand-danger;
            padding: 0 4px;
        }
    }
}
.flow-bline {
    margin-bottom: (@line-height-computed / 2);
    height: 0;
    border-bottom: 1px solid @gray-lighter;
    font-size: 0;
}
.flow-box-right {
  .flow-box-input{
      margin:0;
      padding: 4px 6px;
      border:none;
      position: absolute;
      left:1px;
      top:1px;
      z-index:10;
      width:75%;
      outline:none;
  }
    .flow-group-btn-group();
    .btn-group .dropdown-menu {
        padding: 0;
        &.dropdown-menu-static{
            padding:0 10px;
        }
        > li {
            padding: 5px 0;
        }
    }
}
.flow-group-btn-group {
    .btn-group {
        width: 100%;
        margin-top: 10px;
        &:first-child {
            margin-top: 0;
        }
        .btn[data-toggle="dropdown"],
        .dropdown-menu {
            width: 100%;
            border-radius: 0;
        }

        .btn {
            .box-shadow(none);
            &.btn-border{
                border-color:#bfbfbf;
                color:#333;
            }
            > span > i {
                color: @gray-light;
            }
        }
        .dropdown-menu {
            margin-top: -1px;
            padding: 5px;
            > li {
                padding: 5px 0 5px 5px;
                label {
                    padding: 2px 5px;
                    margin: 2px 10px;
                    border: 1px solid @gray-lighter;
                    input {
                        margin-right: 6px;
                    }
                }
              .menulist{
                padding:0px 0px;
                margin:0px 0px;
                background:none;
                border-bottom:none;
                .btn-group{
                  border-top:solid 1px #ffffff;
                  &.open{
                    .btn{
                      border-color:#83bde4;
                      border-bottom:none;
                      background:#f4f5f7;
                        z-index:1003;
                        color:@gray;
                    }
                  }
                  .btn{
                    text-align: left;
                    padding:6px;
                      background-color: transparent;
                      .caret{
                          margin-top: 7px;
                      }
                  }

                  .dropdown-menu{
                    min-width: 100%;
                    margin-top:-7px;
                    background:none;
                      top:42px;
                    border:none;
                      ul{
                          margin:0 0px;
                          background:#f4f5f7;
                          border:#83bde4 1px solid;
                          padding:0;
                          li{
                              label{
                                  padding:2px 0;
                                  margin:2px 0;
                              }
                          }
                      }
                  }
                }
              }
            }
        }
        &.active {
            > .btn {
                border-color: @brand-warning;
                > span > i {
                    color: @brand-warning;
                }
            }
        }
    }
}
.flow-group-menu {
    .flow-group-btn-group();
    textarea.form-control {
        width: 100%;
        border-radius: 0;
    }
    span.text-count{
        position:absolute;
        right: 6px;
        bottom: 6px;
        .text-count-word{
            color: @brand-danger;
            padding: 0 4px;
        }
    }
    &.flow-group-organiz{
        .btn-group{
            width: 100%;
            margin-top: 10px;
            .btn[data-toggle="show"]{
                label{margin: 0}
            }
            .btn[data-toggle="show"],
            .dropdown-menu {
                width: 100%;
                border-radius: 0;
                &.dropdown-menu-show {
                    position: relative;
                    display: block!important;
                    >li{
                        label{
                            padding: 2px 5px;
                            margin: 2px 5px;
                        }
                    }
                }
            }
        }
    }
}

.flow-group-wrap {
    border: 1px solid @gray-lighter;
    padding: 10px;
    margin-top: 10px;

    .btn-group {
        width: 48.5%;
        margin-top: 0;
        display: inline-block;
        float: right;
        &:first-child {
            float: left;
        }
        .dropdown-menu {
            padding: 0;
            > li {
                label {
                    margin: 0;
                    input {
                        margin-right: 4px;
                    }
                }
            }
        }
    }
}

.flow-group-btn {
    margin-top: 10px;
    .btn {
        margin-left: 5px;
        margin-right: 5px;
    }
}

.flow-steps {
    width: 32px;
    position: absolute;
    top: 40%;
    right: 1px;
    border: 1px solid #e4e4e4;
    a {
        display: block;
        width: 32px;
        height: 44px;
        line-height: 17px;
        padding: 5px 0;
        background-color: @white;
        text-align: center;
        text-decoration: none;
        color: @gray;
        &.active {
            padding-top: 15px;
            background-color: @brand-success;
            i {
                font-size: 1.5rem;
                color: @white;
            }
        }
        &.next {
            background-color: @brand-primary;
            color: @white;
        }
    }
}
.dropdown-menu-static{
    li{
        position:relative;
        margin:0;
        padding: 0 6px;
        .static{
            position: static;
        }
    }
    .btn-group{
        position: static;
        .dropdown-menu{
            li{
                label{border:none;}
            }
        }

    }
}

.flow-group-list{
    .btn-group{
        width: 100%;
    }
}
.dropdown-menu-static{
    li{
        position:relative;
        margin:0;
        padding: 0 6px;
    }
    .btn-group{
        position: static;
        .dropdown-menu{
            li{
                label{border:none;}
            }
        }

    }
}

.sino-flow{
    .flow-state{
        display: none;
    }
    .flow-prompt{
        min-height: 100px;
        padding-top: 40px;
    }
    .flow-title{
        padding-left: 25px;
        li{
            h3{
                color: #F9A52B;
                >i{
                    position: absolute;
                    left: -6px;
                    top:5px;
                }
            }
        }
    }
    .timeline-item-divider{
        border-left: solid 1px #FAA429;
    }
    div,ul {
        margin-bottom: 0;
        &.flow-box-left {
            width: 8.2%;
        }
        &.flow-box-right {
            width: 91.8%;
            .btn-group{
                &.half{
                    width: 58.3%;
                    float: left;
                }
                .department-item{
                    label{
                        display: block;
                        padding: 2px 0px;
                        margin: 2px 0px;
                        border:none;
                        color:@text-color;

                    }
                }
            }
        }
        &.flow-bline{
            margin-left: -10px;
            margin-right: -10px;
            margin-bottom: (@line-height-computed / 2);
            height: 0;
            border-bottom: 1px solid @gray-lighter;
        }
    }
    .selectvalues-box{
        position: absolute;
        width: 99%;
        margin-left: 1px;
        padding: 5px 5px;
        font-size: 12px;
        z-index: 3;
        white-space: nowrap;
        overflow: hidden;
        margin-top:1px;
        .selected{

            span{
                padding-right:5px;
                a{
                    text-decoration: none;
                    color: @text-color;
                    padding-right: 3px;
                }
                input{
                    background: #CCCCCC;
                    color: #fff;
                    width: 14px;
                    height: 14px;
                    border-radius: 10px;
                    border: none;
                    text-align: center;
                    padding: 0;
                    line-height: 13px;
                    font-weight: bold;

                }
                &:hover{
                    input{
                        background:#66B3DF;
                    }
                    a{
                        font-weight: bold;
                        color: @text-color;
                    }
                }
            }
        }
    }
    .input-group{
        .input-group-addon{
            position: relative;
            z-index: 4;
        }
    }
    .menulist{
        margin-left: -5px;
        margin-right: -5px;
        .btn-group{
            width: 100%;
            .btn{
                width: 100%;
                outline: none;
                text-align: left;
                background:transparent;
                padding: 2px 6px;
                color: @text-color;
                &.active{
                    background: #DFF0FA;
                }
                >i{
                    margin-right: 0;
                    &.fa-sort-desc{

                    }
                }
            }
        }
        .dropdown-toggle{
            > li {
                padding: 5px 0 0 0;
                label {
                    padding: 2px 5px;
                    margin: 0;
                    border: none;
                    color: @text-color;
                    input {
                        margin-right: 6px;
                    }
                }
            }
        }

    }
    .dropdown-menu-static{
        background: #F8FCFF;
        border-color: #67B1DE;
        max-height: 180px;
        overflow-y: scroll;
        >li{
            padding:0;
        }
    }
}
.mt-5{
    margin-top:5px;
}
.pb-5{
    padding-bottom: 5px;
}





/*2017-10-21新增select2*/
.select2-container {
    box-sizing: border-box;
    //display: inline-block;
    margin: 0;
    position: relative;
    vertical-align: middle; }
.select2-container .select2-selection--single {
    box-sizing: border-box;
    cursor: pointer;
    display: block;
    height: 28px;
    user-select: none;
    -webkit-user-select: none; }
.select2-container .select2-selection--single .select2-selection__rendered {
    display: block;
    padding-left: 8px;
    padding-right: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }
.select2-container .select2-selection--single .select2-selection__clear {
    position: relative; }
.select2-container[dir="rtl"] .select2-selection--single .select2-selection__rendered {
    padding-right: 8px;
    padding-left: 20px; }
.select2-container .select2-selection--multiple {
    box-sizing: border-box;
    cursor: pointer;
    display: block;
    //height: 20px;
    user-select: none;
    -webkit-user-select: none; }
.select2-container .select2-selection--multiple .select2-selection__rendered {
    display: inline-block;
    //overflow: hidden;
    padding-left: 0px;
    //white-space: nowrap;
}
.select2-container .selection.form-control.input-sm.input-select {
    height: auto;
}
.select2-container .select2-search--inline {
    display:inline-block; }
.select2-container .select2-search--inline .select2-search__field {
    box-sizing: border-box;
    border: none;
    font-size: 100%;
    padding: 0; }
.select2-container .select2-search--inline .select2-search__field::-webkit-search-cancel-button {
    -webkit-appearance: none; }

.select2-dropdown {
    border-radius: 4px;
    box-sizing: border-box;
    display: block;
    position: absolute;
    left: -100000px;
    width: 100%;
    z-index: 1051;
    max-height: 180px;
    overflow-y: scroll;
    background: #f8fcff;
    border: 1px solid #67b1de;}

.select2-results {
    display: block; }

.select2-results__options {
    list-style: none;
    margin: 0;
    padding:10px 0; }

.select2-results__option {
    padding: 6px;
    user-select: none;
    -webkit-user-select: none; }
.select2-results__option[aria-selected] {
    cursor: pointer; }

.select2-container--open .select2-dropdown {
    left: 0; }

.select2-container--open .select2-dropdown--above {
    border-bottom: none;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0; }

.select2-container--open .select2-dropdown--below {
    border-top: none;
    border-top-left-radius: 0;
    border-top-right-radius: 0; }

.select2-search--dropdown {
    display: block;
    padding: 4px; }
.select2-search--dropdown .select2-search__field {
    padding: 4px;
    width: 100%;
    box-sizing: border-box; }
.select2-search--dropdown .select2-search__field::-webkit-search-cancel-button {
    -webkit-appearance: none; }
.select2-search--dropdown.select2-search--hide {
    display: none; }

.select2-close-mask {
    border: 0;
    margin: 0;
    padding: 0;
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    min-height: 100%;
    min-width: 100%;
    height: auto;
    width: auto;
    opacity: 0;
    z-index: 99;
    background-color: #fff;
    filter: alpha(opacity=0); }

.select2-hidden-accessible {
    border: 0 !important;
    clip: rect(0 0 0 0) !important;
    height: 1px !important;
    margin: -1px !important;
    overflow: hidden !important;
    padding: 0 !important;
    position: absolute !important;
    width: 1px !important; }

.select2-container--default .select2-selection--single {
    background-color: #fff;
    //border: 1px solid #aaa;
    border-radius: 4px; }
.select2-container--default .select2-selection--single .select2-selection__rendered {
    color: #444;
    line-height: 28px; }
.select2-container--default .select2-selection--single .select2-selection__clear {
    cursor: pointer;
    float: right;
    font-weight: bold; }
.select2-container--default .select2-selection--single .select2-selection__placeholder {
    color: #999; }
.select2-container--default .select2-selection--single .select2-selection__arrow {
    height: 26px;
    position: absolute;
    top: 1px;
    right: 1px;
    width: 20px; }
.select2-container--default .select2-selection--single .select2-selection__arrow b {
    border-color: #888 transparent transparent transparent;
    border-style: solid;
    border-width: 5px 4px 0 4px;
    height: 0;
    left: 50%;
    margin-left: -4px;
    margin-top: -2px;
    position: absolute;
    top: 50%;
    width: 0; }

.select2-container--default[dir="rtl"] .select2-selection--single .select2-selection__clear {
    float: left; }

.select2-container--default[dir="rtl"] .select2-selection--single .select2-selection__arrow {
    left: 1px;
    right: auto; }

.select2-container--default.select2-container--disabled .select2-selection--single {
    background-color: #eee;
    cursor: default; }
.select2-container--default.select2-container--disabled .select2-selection--single .select2-selection__clear {
    display: none; }

.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
    border-color: transparent transparent #888 transparent;
    border-width: 0 4px 5px 4px; }

.select2-container--default .select2-selection--multiple {
    background-color: white;
    cursor: text; }
.select2-container--default .select2-selection--multiple .select2-selection__rendered {
    box-sizing: border-box;
    list-style: none;
    margin: 0;
    width: 100%;
    padding-right: 10px;
}
.select2-container--default .select2-selection--multiple .select2-selection__rendered li {
    list-style: none; }
.select2-container--default .select2-selection--multiple .select2-selection__placeholder {
    color: #999;
    margin-top: 5px;
    float: left; }
.select2-container--default .select2-selection--multiple .select2-selection__clear {
    cursor: pointer;
    font-weight: bold;
    z-index: 1000;
    background: #fff;
    position: absolute;
    right: 5px}
.select2-container--default .select2-selection--multiple .select2-selection__choice {
    cursor: default;
    display: inline-block;
}
.select2-container--default .select2-selection--multiple .select2-selection__choice:hover{
    color: #000;
    font-weight: bold;
}
.select2-container--default .select2-selection--multiple .select2-selection__choice:hover span {
    background: #66b3df;
}
.select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-weight: bold;
    margin-right: 4px;
    float: right;
    border-radius: 10px;
    width: 14px;
    height: 14px;
    line-height: 13px;
    text-align: center;
    background: #ccc;
    margin-top:3px;
    margin-left: 1px;
    padding-right: 1px;
}
.select2-container--default .select2-selection--multiple .select2-selection__choice__remove:hover {
    color: #333; }

.select2-container--default[dir="rtl"] .select2-selection--multiple .select2-selection__choice, .select2-container--default[dir="rtl"] .select2-selection--multiple .select2-selection__placeholder, .select2-container--default[dir="rtl"] .select2-selection--multiple .select2-search--inline {
    float: right; }

.select2-container--default[dir="rtl"] .select2-selection--multiple .select2-selection__choice {
    margin-left: 5px;
    margin-right: auto; }

.select2-container--default[dir="rtl"] .select2-selection--multiple .select2-selection__choice__remove {
    margin-left: 2px;
    margin-right: auto; }

.select2-container--default.select2-container--focus .select2-selection--multiple {
    outline: 0; }

.select2-container--default.select2-container--disabled .select2-selection--multiple {
    background-color: #eee;
    cursor: default; }

.select2-container--default.select2-container--disabled .select2-selection__choice__remove {
    display: none; }

.select2-container--default.select2-container--open.select2-container--above .select2-selection--single, .select2-container--default.select2-container--open.select2-container--above .select2-selection--multiple {
    border-top-left-radius: 0;
    border-top-right-radius: 0; }

.select2-container--default.select2-container--open.select2-container--below .select2-selection--single, .select2-container--default.select2-container--open.select2-container--below .select2-selection--multiple {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0; }

.select2-container--default .select2-search--dropdown .select2-search__field {
    border: 1px solid #aaa; }

.select2-container--default .select2-search--inline .select2-search__field {
    background: transparent;
    border: none;
    outline: 0;
    box-shadow: none;
    -webkit-appearance: textfield;
}


.select2-container--default .select2-results__option[role=group] {
    padding: 0; }

